<template>
  <div class="good">
    <aside>
      <ul>
        <li
          v-for="(item, index) in goods"
          :key="index"
          :class="{ cli: cur == index ? 1 : 0 }"
          @click="cli(index)"
        >
          <div>{{ item.name }}</div>
        </li>
      </ul>
    </aside>
    <section style="position: relative">
      <ul>
        <li class="out" v-for="(item, index) in goods" :key="index">
          <ul>
            <li class="insetTop">{{ item.name }}</li>
            <li
              class="insetBody"
              v-for="(val, index1) in item.foods"
              :key="index1"
              @click="goodsDetail(val,index,index1)"
            >
              <img :src="val.icon" alt="" />
              <span>
                <h4>{{ val.name }}</h4>
                <p>{{ val.description }}</p>
                <div>
                  月售{{ val.sellCount }}&nbsp;&nbsp;好评{{ val.rating }}
                </div>
                <span>￥{{ val.price }}</span>
                <span class="addCart" @click.stop="addCart($event, val)"
                  >+</span
                >
                <transition name="move">
                  <span
                    class="minusCart"
                    v-show="val.count>0"
                    @click.stop="minus(val)"
                    >-</span
                  >
                </transition>
                <span class="count" v-show="val.count > 0" @click.stop="fn">{{
                  val.count
                }}</span>
                <div v-for="(ball, index) in balls" :key="index" @click.stop="fn">
                  <transition
                    name="drop"
                    @before-enter="beforeEnter"
                    @enter="enter"
                    @after-enter="afterEnter"
                  >
                    <div class="ball" v-show="ball.show">
                      <div class="inner inner-hook"></div>
                    </div>
                  </transition>
                </div>
              </span>
            </li>
          </ul>
        </li>
      </ul>
    </section>
    <!-- <router-view></router-view> -->
  </div>
</template>

<script>
import { mapState } from "vuex";
// import router from './router/index.js'
export default {
  props: ["goods"],
  data() {
    return {
      cur: 0,
      balls: [
        { show: false }, //show为false表示球处于空闲状态，可以做抛物线运动
        { show: false },
        { show: false },
        { show: false },
        { show: false },
      ],
      sumCount: 0,
      dropBalls: [],
      arrLength: [],
      sumlength: [],
      sumClick:0,
      
    };
  },
  
  created() {
    this.goods.forEach((val) => {
      val.foods.forEach((item) => {
        if(!('count' in item)){
        this.$set(item, "count", 0);
        }else{
          
         /*  if(item.count>0){
            console.log(item);
          this.sumClick+=item.count;
            this.sumCount+=item.count*item.price;
            item.isMove=true;
          } */
        }
      // this.$store.commit('change',[this.sumCount/2,this.sumClick/2]);
        if(!('isMove' in item))
        this.$set(item, "isMove", false);
      });
    });
  },
  mounted() {
    // console.log(this.sumCount);
    var _this = this;
    document.querySelectorAll(".out").forEach((val) => {
      _this.arrLength.push(val.offsetHeight);
    });
    console.log( document.getElementsByClassName("out").length);
    console.log(_this.arrLength);
    this.sumlength[0] = 0;
    for (let i = 1; i <= this.arrLength.length; i++) {
      this.sumlength.push(this.arrLength[i - 1] + this.sumlength[i - 1]);
    }
    document.querySelector("section").addEventListener("scroll", function () {
      var scrollTop = document.querySelector("section").scrollTop;
      for (let i = 1; i <= _this.sumlength.length; i++) {
        if (
          scrollTop >= _this.sumlength[i - 1] &&
          scrollTop < _this.sumlength[i]
        ) {
          _this.cur = i - 1;
          break;
        }
      }
    });
  },
  destroyed(){
    this.$emit('getGood', this.goods);
  },
  methods: {
    fn(){

    },
    sumLength(index) {
      var l = 0;
      for (let i = 0; i < index; i++) {
        l += this.arrLength[i];
      }
      return l;
    },
    cli(index) {
      this.cur = index;
      document.querySelector("section").scrollTop = this.sumLength(index);
    },
    addCart($event, val) {
      // this.sumClick++;
      val.isMove = true;
      val.count++;
      // this.sumCount += val.price;
      this.$store.commit('change',[val.price,1]);
      for (let i = 0; i < this.balls.length; i++) {
        let ball = this.balls[i];
        if (!ball.show) {
          ball.show = true;
          ball.el = $event.target;
          this.dropBalls.push(ball);
          return;
        }
      }
    },
    minus(val) {
      if (val.count > 1) {
        val.count--;
        // this.sumClick--;
        // this.sumCount-=val.price;
      this.$store.commit('change',[-val.price,-1]);
      } else if (val.count == 1) {
        val.count = 0;
        // this.sumClick--;
        // this.sumCount-=val.price;
        val.isMove = false;
      this.$store.commit('change',[-val.price,-1]);
      } else {
        val.isMove = false;
      }
      // this.$store.commit('change',[this.sumCount,this.sumClick]);
    },
    beforeEnter(el) {
      /* 把底部小球平移到添加购物车‘+’ */
      for (let i = 0; i < this.balls.length; i++) {
        let ball = this.balls[i];
        if (ball.show) {
          let rect = ball.el.getBoundingClientRect();
          let x = rect.left - 32;
          let y = -(window.innerHeight - rect.top - 40);
          el.style.display = "";
          el.style.transform = `translate3d(0,${y}px,0)`;
          let inner = el.getElementsByClassName("inner-hook")[0];
          inner.style.transform = `translate3d(${x}px,0,0)`;
        }
      }
    },
    enter(el) {
      /* 触发浏览器重绘 */
      let rf = el.offsetHeight;
      this.$nextTick(function () {
        el.style.transform = `translate3d(0,0,0)`;
        let inner = el.getElementsByClassName("inner-hook")[0];
        inner.style.transform = `translate3d(0,0,0)`;
      });
    },
    afterEnter(el) {
      let ball = this.dropBalls.shift();
      if (ball) {
        ball.show = false;
        el.style.display = "none";
      }
    },
    goodsDetail(val,index,index1){
        this.$set(val, "ins", index);
        this.$set(val, "int", index1);
        this.$router.push({ path: `/goods/${val.name}`,query:val });
    },
  },
};
</script>
