import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    count: 0,
    sumclick: 0,
  },
  mutations: {
    change(state, sum) {
      state.count +=  Number(sum[0]);
      state.sumclick += sum[1];
    },
    init(state) {
      state.count = 0;
      state.sumclick = 0;
    },
    yj(state,s) {
      state.count += Number(s[0]);
      state.sumclick += s[1];
    },
  },
  actions: {
  },
  modules: {
  }
})
