<template>
  <div class="app">
    <headtop :imgdata="imgdata"></headtop>
    <main>
      <router-view :goods="goods" :rats="rat" :imgdata="imgdata" @getGood="getGood" @newcount="newcount" ></router-view>
    </main>
    <footer>
      <foot :goods="goods" @goodscountchange="goodscountchange"></foot>
    </footer>
  </div>
</template>

 <script>
import headtop from '@/views/headtop';
import foot from '@/views/foot';
import data1 from '@/data.json';

export default {
  components:{
      headtop, 
      foot,
  },
 data(){
    return{
        imgdata:{},
        goods:{},
        rat:{},
    }
  },
  created(){
      this.imgdata = data1.seller;
      this.goods = data1.goods;
      this.rat = data1.ratings;
     
  },
  methods:{
    goodscountchange(){
      this.as = 0;
      for(let i of this.goods){
        for(let val of i.foods){
          val.count =0;
        }
        
      }
      
    },
    getGood(val){
      this.goods = val;
    },
    newcount(val){
     
      for(let i of this.goods[Number(val[2])].foods){
        if(i.name == val[1])
          {
            i.count = Number(val[0]);
            if(i.count>0){//注：isMove弃用
              i.isMove=true;
            }
            else{
              i.isMove = false;
            }
          }
      }
      /* for(let i of this.goods){
        for(let v of i.foods){
          if(v.name == val[1])
          {
            v.count = val[0];
          }
        }
        
      } */
    },
    
  }

}
</script>

<style lang="scss" scoped>
  
  main{
    position: absolute;
    top:200px;
    bottom:50px;
    width: 100%;
  }
</style>
