<template>
  <div class="model" :class="{ model1: ismodel }">
    <div class="Rng" v-if="ismodel">
      <h4>{{ information.name }}</h4>
      <star :score="information.score" :key="information.name"></star>
      <span class="sellInfo">
        <div></div>
        <span>优惠信息</span>
        <div></div>
      </span>
      <ul>
        <li v-for="item in information.supports" :key="item.keys">
          <div></div>
          <span>{{ item.description }}</span>
        </li>
      </ul>
      <span class="sellInfo">
        <div></div>
        <span>商家公告</span>
        <div></div>
      </span>
      <p>{{ information.bulletin }}</p>
      <div @click="fn" class="cancle">
        <i class="el-icon-close"></i>
      </div>
    </div>
  </div>
</template>
<script>
import star from "@/views/star";
export default {
  name: "MyvueModel",
  props: ["ismodel", "information"],
  components: {
    star,
  },
  data() {
    return {};
  },

  mounted() {
    if (document.querySelector(".model")) {
      document.querySelector(".model").style.height =
        window.screen.height + "px";
    }
    if (document.querySelector(".model1")) {
      document.querySelector(".model1").style.height =
        window.screen.height + "px";
    }
  },

  methods: {
    fn() {
      this.$emit("ismodels");
    },
  },
};
</script>
<style lang="scss" scoped>
.model {
  width: 100%;
  bottom: 0;
  left: 0;

  position: absolute;
  background-color: rgba(44, 44, 44, 0.815);
  z-index: -1;
}
.model1 {
  z-index: 99;
  bottom: auto;
}
.cancle {
  position: absolute;
  bottom: 30px;
  font-size: 30px;
  color:white;
  font-weight: 550;
}
.Rng {
  width: 90%;
  height: 100%;
  box-sizing: border-box;
  padding: 20px;
  margin: 0px auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  h4 {
    color: white;
    margin-top: 100px;
    font-weight: 550;
    font-size: 18px;
  }
  .star ::v-deep span {
    display: inline-block;
    margin-left: 15px;
    padding-top: 20px;
    img {
      width: 30px;
      height: 30px;
    }
  }
  .sellInfo {
    width: 100%;
    margin-top: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    & div {
      width: 38%;
      height: 1px;
      background-color: #ccc;
    }
    & span {
      font-size: 14px;
      font-weight: bold;
      color: white;
      display: block;
      padding: 0 10px;
    }
  }
  ul {
    margin-top: 25px;
    li {
      display: flex;
      margin-bottom: 10px;
      div {
        width: 20px;
        height: 20px;
        background-color: red;
      }
      span {
        margin-left: 10px;
        color: #ccc;
        font-size: 14px;
      }
    }
  }
  p {
    font-size: 12px;
    color: white;
    width: 90%;
    margin: 0 auto;
    margin-top: 20px;
    letter-spacing: 0.2em;
    line-height: 16px;
    text-indent: 2em;
  }
}
</style>