<template>
  <div class="goodsList">
    <img :src="this.$route.query.icon" alt="" />
    <i class="el-icon-arrow-left" @click="backGoods"></i>
    <div class="buy">
      <div>{{ this.$route.query.name }}</div>
      <div>
        <span>月售{{ this.$route.query.sellCount }}份</span>
        <span>好评率{{ this.$route.query.rating }}%</span>
      </div>
      <div class="add">
        <span>￥{{ this.$route.query.price }}</span>
        <span>
          <div
            class="btn"
            @click="addGood"
            v-if="this.$route.query.count < 1"
          >
            加入购物车
          </div>
          <span v-if="this.$route.query.count > 0">
            <span class="addCart" @click.stop="addCart($event)">+</span>
            <span class="minusCart" @click.stop="minus(1)">-</span>
            <span class="count">{{ this.$route.query.count }}</span>
            <div v-for="(ball, index) in balls" :key="index" @click.stop="fn">
              <transition
                name="drop"
                @before-enter="beforeEnter"
                @enter="enter"
                @after-enter="afterEnter"
              >
                <div class="ball" v-show="ball.show">
                  <div class="inner inner-hook"></div>
                </div>
              </transition>
            </div>
          </span>
        </span>
      </div>
    </div>
    <div class="bottomText" v-if="this.$route.query.info">
      <div>商品信息</div>
      <p>{{ this.$route.query.info }}</p>
    </div>
    <div class="goodrat"></div>
  </div>
</template>
<script>
export default {
  name: "MyvueGoodslist",

  data() {
    return {
      ishow: true,
      goods: [],
      balls: [
        { show: false }, //show为false表示球处于空闲状态，可以做抛物线运动
        { show: false },
        { show: false },
        { show: false },
        { show: false },
      ],
      dropBalls: [],
    };
  },

  created() {
    this.goods = this.$route.query;
    this.sumCount = this.sumPrice;
    this.sumClick = this.sumclick;
  },
  mounted() {
    if (document.querySelector(".goodsList")) {
      document.querySelector(".goodsList").style.height =
        window.screen.height + "px";
    }
  },

  methods: {
    backGoods() {
      this.$router.push({ path: `/goods/` });
    },
    addGood() {
      this.ishow = !this.ishow;
      this.$route.query.count++;
      this.$emit("newcount", [
        this.$route.query.count,
        this.$route.query.name,
        this.$route.query.ins,
        this.$route.query.int,
      ]);

      this.$store.commit("yj", [this.$route.query.price, 1]);
    },
    addCart($event) {
      this.$route.query.count++;
     
      this.$emit("newcount", [
        this.$route.query.count,
        this.$route.query.name,
        this.$route.query.ins,
        this.$route.query.int,
      ]);

      this.$store.commit("yj", [this.$route.query.price, 1]);
      for (let i = 0; i < this.balls.length; i++) {
        let ball = this.balls[i];
        if (!ball.show) {
          ball.show = true;
          ball.el = $event.target;
          this.dropBalls.push(ball);
          return;
        }
      }
    },
    minus() {
      if (this.$route.query.count > 0) {
        this.$route.query.count--;
        this.$store.commit("yj", [-this.$route.query.price, -1]);
      } else {
        this.$route.query.count = 0;
        this.ishow = true;
      }

      this.$emit("newcount", [
        this.$route.query.count,
        this.$route.query.name,
        this.$route.query.ins,
        this.$route.query.int,
      ]);
    },
    beforeEnter(el) {
      /* 把底部小球平移到添加购物车‘+’ */
      for (let i = 0; i < this.balls.length; i++) {
        let ball = this.balls[i];
        if (ball.show) {
          let rect = ball.el.getBoundingClientRect();
          let x = rect.left - 32;
          let y = -(window.innerHeight - rect.top - 40);
          el.style.display = "";
          el.style.transform = `translate3d(0,${y}px,0)`;
          let inner = el.getElementsByClassName("inner-hook")[0];
          inner.style.transform = `translate3d(${x}px,0,0)`;
        }
      }
    },
    enter(el) {
      /* 触发浏览器重绘 */
      let rf = el.offsetHeight;
      this.$nextTick(function () {
        el.style.transform = `translate3d(0,0,0)`;
        let inner = el.getElementsByClassName("inner-hook")[0];
        inner.style.transform = `translate3d(0,0,0)`;
      });
    },
    afterEnter(el) {
      let ball = this.dropBalls.shift();
      if (ball) {
        ball.show = false;
        el.style.display = "none";
      }
    },
  },
};
</script>
<style lang="scss" scoped>
.goodsList {
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: absolute;
  top: -200px;
  left: 0;
  border: 1px solid #ccc;
  background-color: rgb(233, 233, 233);
  overflow-y: scroll;
  .el-icon-arrow-left {
    position: absolute;
    top: 30px;
    left: 20px;
    color: white;
    font-size: 20px;
  }
  img {
    width: 100%;
    height: 60%;
  }
  .buy {
    margin-top: 20px;
    height: 100px;
    width: 100%;
    padding: 10px;
    border-top: 1px solid #ccc;
    border-bottom: 1px solid #ccc;
    background-color: white;
    box-sizing: border-box;
    & > div:first-child {
      font-weight: bold;
    }
    div:nth-of-type(2) {
      margin-top: 10px;
      font-size: 14px;
      span:nth-of-type(2) {
        margin-left: 20px;
      }
    }
  }
  .add {
    display: flex;
    justify-content: space-between;
    margin-top: 8px;
    position: relative;
    & > span:first-child {
      color: red;
    }
    & > span:last-child {
      .btn {
        color: white;
        padding: 3px;
        width: 100px;
        font-size: 14px;
        text-align: center;
        height: auto;
        background-color: rgb(45, 127, 235);
        border-radius: 14px;
      }
    }
    .addCart {
      width: 16px;
      height: 16px;
      border-radius: 50%;
      display: flex;
      align-items: flex-end;
      justify-content: center;
      border: 1px solid #0099cc;
      background-color: #0099cc;
      position: absolute;
      right: 0;
      top: 0;
      color: white;
    }
    .minusCart {
      width: 16px;
      height: 16px;
      border-radius: 50%;
      display: flex;
      align-items: flex-end;
      justify-content: center;
      border: 1px solid #0099cc;
      position: absolute;
      right: 60px;
      top: 0px;
      color: #0099cc;
    }
    .count {
      width: 16px;
      height: 16px;
      position: absolute;
      right: 35px;
      top: 0px;
      font-size: 14px;
      color: black;
    }
    .ball {
      position: fixed;
      bottom: 22px;
      left: 32px;
      z-index: 150;
      transition: all 1s cubic-bezier(0.34, -0.56, 0.73, 0.62);
    }

    .inner {
      width: 14px;
      height: 14px;
      border-radius: 50%;
      background-color: rgb(61, 159, 240);
      transition: all 1s linear;
    }
  }
  .bottomText {
    box-sizing: border-box;
    padding: 10px 20px;
    width: 100%;
    height: auto;
    margin-top: 20px;
    background-color: white;
    & > div:first-child {
      font-size: 14px;
    }
    & > p {
      width: 100%;
      padding: 10px;
      margin-top: 20px;
      font-size: 12px;
    }
  }
}
</style>