<template>
  <div class="good">
    <div class="all">
      <div class="allLeft">
        <span>{{ imgdata.score }}</span>
        <p>综合评分</p>
        <p>高于周边商家{{ imgdata.rankRate }}%</p>
      </div>
      <div class="allRight">
        <span
          >服务态度<star
            :score="imgdata.serviceScore"
            :key="imgdata.serviceScore"
          ></star
        ></span>
        <span
          >商品评分<star
            :score="imgdata.foodScore"
            :key="imgdata.foodScore"
          ></star
        ></span>
        <span
          >送达时间<span>{{ imgdata.deliveryTime }}分钟</span></span
        >
      </div>
    </div>
    <div class="goodTop">
      <button @click="fliterList(0)">全部{{ length1 }}</button>
      <button @click="fliterList(1)">满意{{ length2 }}</button>
      <button @click="fliterList(2)">不满意{{ length3 }}</button>
    </div>
    <div class="ratingmid">
      <div class="istrue" @click="selectList" :class="{ isBlue: isOk }">√</div>
      <span>只看内容的评价</span>
    </div>
    <hr />
    <div class="ratingList">
      <ul>
        <li v-for="(item, index) in rat" :key="index">
          <div class="ratingUserTop">
            <span class="imgSpan">
              <img :src="item.avatar" alt="" />
              <span>
                <p>{{ item.username }}</p>
                <span><star :score="item.score" :key="index"></star></span>
                <span v-if="item.deliveryTime"
                  >{{ item.deliveryTime }}分钟送达</span
                >
              </span>
            </span>
            <span>{{ item.rateTime }}</span>
          </div>
          <div class="ratingUserBottom">
            <p>{{ item.text }}</p>
            <span v-for="(val, index1) in item.recommend" :key="index1">{{
              val
            }}</span>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>
  
  <script>
import moment from "moment";
import star from "@/views/star";
export default {
  props: ["rats", "imgdata"],
  components: {
    star,
  },
  data() {
    return {
      length1: 0,
      length2: 0,
      length3: 0,
      rat: {},
      hasrat: {},
      isOk: false,
      num: 0,
    };
  },
  methods: {
    selectList() {
      this.isOk = !this.isOk;
      if (this.isOk) {
        this.rat = this.rat.filter((val) => {
          return val.text.length > 0;
        });
      } else {
        //待修定
        switch (this.num) {
          case 0: {
            this.rat = this.rats;
            break;
          }
          case 1: {
            this.rat = this.rats.filter((val) => {
              return val.rateType == 0;
            });
            break;
          }
          case 2: {
            this.rat = this.rats.filter((val) => {
              return val.rateType == 1;
            });
            break;
          }

          default:
            break;
        }
      }
    },
    fliterList(index) {
      switch (index) {
        case 0: {
          if (this.isOk) {
            this.rat = this.rats.filter((val) => {
              return val.text.length > 0;
            });
          } else {
            this.rat = this.rats;
          }
          this.num = 0;
          break;
        }
        case 1: {
          if (!this.isOk) {
            this.rat = this.rats.filter((val) => {
              return val.rateType == 0;
            });
          } else {
            this.rat = this.rats.filter((val) => {
              return val.rateType == 0;
            });
            this.rat = this.rat.filter((val) => {
              return val.text.length > 0;
            });
          }

          this.num = 1;
          break;
        }
        case 2: {
          if (this.isOk) {
            this.rat = this.rats.filter((val) => {
              return val.rateType == 1;
            });
            this.rat = this.rat.filter((val) => {
              return val.text.length > 0;
            });
          }
          else{
            this.rat = this.rats.filter((val) => {
            return val.rateType == 1;
          });
          }
          
          this.num = 2;
          break;
        }

        default:
          break;
      }
    },
  },
  created() {
    this.rat = this.rats.filter((val) => {
      return val.rateType == 0;
    });

    this.rats.forEach((val) => {
      val.rateTime = moment(val.rateTime).format("YYYY-MM-DD HH:mm:ss");
    });
    this.length1 = this.rats.length;
    this.length2 = this.rat.length;
    this.length3 = this.length1 - this.length2;
    this.rat = this.rats;
  },
};
</script>
  <style lang="scss" scoped>
.isBlue {
  color: rgb(13, 146, 255) !important;
  background-color: rgb(218, 218, 218) !important;
}
.good {
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  background-color: rgb(255, 255, 255);
  border: 1px solid #ccc;
  .all {
    height: 120px;
    width: 100%;
    padding: 10px;
    box-sizing: border-box;
    display: flex;
    border-bottom: 1px solid #ccc;
    .allLeft {
      width: 140px;
      height: 100%;
      border-right: 1px solid #ccc;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      font-size: 12px;
      color: #ccc;
      span {
        font-weight: bolder;
        font-size: 30px;
        color: rgb(255, 145, 0);
      }
    }
    .allRight {
      height: 100%;
      flex: 1;
      display: flex;
      padding: 10px;
      padding-left: 20px;
      font-size: 14px;
      flex-direction: column;
      justify-content: space-around;
      align-items: flex-start;
      & > span {
        display: flex;
        .star ::v-deep span img {
          width: 15px;
          height: 15px;
        }
      }
      & > span:nth-of-type(3) {
        span {
          margin-left: 10px;
          color: #ccc;
        }
      }
    }
  }
  .goodTop {
    width: 90%;
    height: 70px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    border-bottom: 1px solid #ccc;
    button {
      margin-left: 20px;
      height: 30px;
      min-width: 60px;
      font-size: 12px;
      border-radius: 3px;
      border: 0;
    }
    button:nth-of-type(1) {
      margin: 0;
      background-color: rgb(0, 140, 255);
    }
    button:nth-of-type(2) {
      background-color: rgb(126, 190, 243);
    }
    button:nth-of-type(1) {
      background-color: rgb(178, 200, 218);
    }
  }
  .ratingmid {
    height: 30px;
    width: 90%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    .istrue {
      width: 20px;
      height: 20px;
      border-radius: 10px;
      background-color: #bbb;
      color: white;
      font-weight: bold;
      text-align: center;
    }
    span {
      margin-left: 10px;
      color: #ccc;
      font-size: 14px;
    }
  }
  hr {
    background-color: rgb(224, 224, 224);
    height: 1px;
    border: none;
  }
  .ratingList {
    width: 100%;

    & ul {
      li {
        width: 90%;
        box-sizing: border-box;
        margin: 0 auto;
        pad: 10px 0;
        .ratingUserTop {
          width: 100%;
          height: 50px;
          display: flex;
          align-items: center;
          justify-content: space-between;
          font-size: 12px;
          .imgSpan {
            display: flex;
            align-items: center;
            & > span {
              margin-left: 20px;
              .star ::v-deep span img {
                width: 10px;
                height: 10px;
              }
            }
          }
          img {
            width: 50px;
            height: 50px;
          }
        }
        .ratingUserBottom {
          width: 100%;
          box-sizing: border-box;
          padding-left: 70px;
          padding-bottom: 20px;
          p {
            font-size: 13px;
            margin-bottom: 15px;
          }
          span {
            display: inline-block;
            height: 16px;
            border: 1px solid #ccc;
            color: #ccc;
            font-size: 12px;
            padding: 0 5px;
            margin-left: 12px;
            border-radius: 2px;
          }
        }
      }
    }
  }
}
</style>
  