<template>
  <div class="foot">
    <models v-show="isShow && sumPrice"></models>
    <div class="cliCart">
      <div class="shopcart" @click="show">
        <i
          class="el-icon-shopping-cart-full"
          :class="{ blue: sumPrice > 0 }"
        ></i>
      </div>
      <div class="redPoint" v-show="sumPrice > 0">{{ sumclick }}</div>
      <div class="sumPrice">￥{{ sumPrice }}元</div>
      <div class="tips">另需要配送费4元</div>
      <div class="pay" :class="{ goPay: sumPrice >= 20 }">{{ pay }}</div>
    </div>

    <div class="cartList" v-show="isShow && sumPrice">
      <div class="cartTop">
        <span>购物车</span>
        <span @click="cancleAll">清空</span>
      </div>
      <div class="goodsList">
        <div v-for="(item, index) in list" :key="index">
          <span>{{ item.name }}</span>
          <span class="fin">
            <span class="addCart" @click.stop="addCart($event, item)"
                  >+</span
                >
           
                  <span
                    class="minusCart"
                    
                    @click.stop="minus(item,index)"
                    >-</span
                  >
                  <span class="count">{{
                  item.count
                }}</span>
               <div v-for="(ball, index) in balls" :key="index" @click.stop="fn">
                  <transition
                    name="drop"
                    @before-enter="beforeEnter"
                    @enter="enter"
                    @after-enter="afterEnter"
                  >
                    <div class="ball" v-show="ball.show">
                      <div class="inner inner-hook"></div>
                    </div>
                  </transition>
                </div>
          </span>
        </div>
      </div>
    </div>
  </div>
</template>
  
  <script>
import { mapState } from "vuex";
import models from '@/views/modelWindow';
export default {
  components: {
    models,
  },
  props: ["goods"],
  data() {
    return {
      pay: "20元起送",
      isShow: false,
      list: [],
      balls: [
        { show: false }, //show为false表示球处于空闲状态，可以做抛物线运动
        { show: false },
        { show: false },
        { show: false },
        { show: false },
      ],
      dropBalls: [],
    };
  },
  methods: {
    addCart($event, val) {
      val.count++;
      this.$store.commit('change',[val.price,1]);
      for (let i = 0; i < this.balls.length; i++) {
        let ball = this.balls[i];
        if (!ball.show) {
          ball.show = true;
          ball.el = $event.target;
          this.dropBalls.push(ball);
          return;
        }
      }
    },
    minus(val,index) {
      if (val.count > 1) {
        val.count--;
      this.$store.commit('change',[-val.price,-1]);
      } else if (val.count == 1) {
        val.count = 0;
      this.$store.commit('change',[-val.price,-1]);
      this.list.splice(index,1);

      } else {
        // this.list.splice(index,1);
      }
      // this.$store.commit('change',[this.sumCount,this.sumClick]);
    },
    beforeEnter(el) {
      /* 把底部小球平移到添加购物车‘+’ */
      for (let i = 0; i < this.balls.length; i++) {
        let ball = this.balls[i];
        if (ball.show) {
          let rect = ball.el.getBoundingClientRect();
          let x = rect.left - 32;
          let y = -(window.innerHeight - rect.top - 40);
          el.style.display = "";
          el.style.transform = `translate3d(0,${y}px,0)`;
          let inner = el.getElementsByClassName("inner-hook")[0];
          inner.style.transform = `translate3d(${x}px,0,0)`;
        }
      }
    },
    enter(el) {
      /* 触发浏览器重绘 */
      let rf = el.offsetHeight;
      this.$nextTick(function () {
        el.style.transform = `translate3d(0,0,0)`;
        let inner = el.getElementsByClassName("inner-hook")[0];
        inner.style.transform = `translate3d(0,0,0)`;
      });
    },
    afterEnter(el) {
      let ball = this.dropBalls.shift();
      if (ball) {
        ball.show = false;
        el.style.display = "none";
      }
    },
    initAll(){
      this.isShow = false;
      this.list = [];
      
    },

    createList() {
      var _this = this;
      this.goods.forEach((val) => {
        val.foods.forEach((item) => {
          if (item.count>0 && _this.list.indexOf(item) == -1) {
            _this.list.push(item);
          }
        });
      });
    },
    show() {
      
      if (this.sumPrice) {
        this.createList();
        this.isShow = !this.isShow;
      }
     if(!this.isShow){
        this.list.length = 0;
      }
    },
    cancleAll(){
      this.initAll();
      this.$emit('goodscountchange');
      this.$store.commit('init');
      // console.log(this.sumclick);
    }
  },
  computed: mapState({
    sumPrice: "count",
    sumclick: "sumclick",
  }),
  watch: {
    sumPrice() {
      if (this.sumPrice >= 20) {
        this.pay = "支付";
      } else if (this.sumPrice>0){
        this.pay = "20元起送";
      }
      else{
        this.isShow=false;
      }
    },
  },
};
</script>
