<template>
    <div class="star">
        <span v-for="(item,index) in arr" >
            <img src="@/img/star.png" alt="">
        </span>
        <span v-for="(val,inde) in arroff" >
            <img src="@/img/staroff.png" alt="">
        </span>
    </div>
</template>
<script>
export default {
    name: 'MyvueStar',
    props:['score'],
    data() {
        return {
            scores:0,
            arr:[1,1,1,1,1],
            arroff:[1,1,1,1,1],
        };
    },
    created(){
        this.scores = Math.floor(this.score);
        this.arr.length=this.scores;
        this.arroff.length=5-this.scores;
    },
    mounted() {
        
    },

    methods: {
        
    },
};
</script>
